.nav{
    font-weight: 700;
    font-size: 25px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(-20deg, #6e45e2 0%, #617ca0 100%);
    position: fixed;
    z-index: 2000;
    filter: drop-shadow(0 0 0.15rem rgb(63, 62, 63));
  }
.nav a {
    color: white;
    font-weight: 700;
    font-size: 25px;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    text-decoration: none;
    margin: 10px;
  }

  .navPicDiv{
    position: absolute;
    
    left: 10px;
    top: -16.4px;
  }

  .navPic{
    width: 90px;
  }
.nav a:hover{
   opacity: 0.6;
   color: white;
  }

  @media screen and (max-width: 800px){
    .navPicDiv{
      position: absolute;
      left: -20px;
      top: -16.4px;
    }
  }

